export const iconSize = 50
var iconSizePad = 30
var leftRightDiff = 40

export const isMobile = cvWidth => {
  return cvWidth < 640
}

export const getLeftSideDivStyle = cvWidth => {
  var width = isMobile(cvWidth) ? "100%" : iconSize + iconSizePad
  return {
    width,
    display: "inline-block",
    textAlign: "center"
  }
}

export const getRightSideDivStyle = cvWidth => {
  var width = isMobile(cvWidth) ? "100%" : cvWidth - iconSize - iconSizePad - leftRightDiff
  return {
    width,
    display: "inline-block",
  }
}

export const mixWithBorder = style => {
  return Object.assign({
    borderLeft: "3px solid black",
    borderBottom: "1px solid black",
  }, style)
}

export const mixWithBorderAndPadding = style => {
  var borderAndPadding = mixWithBorder({ 
    //padding: "2px 8px 8px 8px" 
  })
  return Object.assign(borderAndPadding, style);
}

